import { FeaturePolicy as apiPolicy } from "../../_api/iam";
import { Policy } from "./policy";

/**
 * Policy Adapter
 */
export class PolicyAdapter {
    public static map(from: apiPolicy): Policy {
        return new Policy({
            action:      from.action,
            allow:       from.allow,
            feature:     from.feature,
            resource:    from.resource
        });
    }
}
